/*RESET BROWSER'S STYLES*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-weight: inherit;
  font-style: inherit;
  font-family: inherit;
  font-size: 100%;
  vertical-align: baseline;
}
body {
  line-height: 1;
  color: #000;
  background: #fff;
}
ol,
ul {
  list-style: none;
}
table {
  border-collapse: separate;
  border-spacing: 0;
  vertical-align: middle;
}
caption,
th,
td {
  text-align: left;
  font-weight: normal;
  vertical-align: middle;
}
a img {
  border: none;
}
/*BASIC STYLES*/
.clearfix {
  zoom: 1;
}
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
html {
  font-size: 100%;
}
body {
  min-width: 320px;
  color: #222;
  font-size: 1.4rem;
  line-height: 1.6;
  font-family: "RalewayRegular", sans-serif;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  scroll-behavior: smooth;
}
/*============================================*/
.toggle-mnu {
  position: relative;
  display: block;
  width: 28px;
  height: 28px;
  margin-top: 14px;
}
.toggle-mnu span:after,
.toggle-mnu span:before {
  content: "";
  position: absolute;
  left: 0;
  top: 9px;
}
.toggle-mnu span:after {
  top: 18px;
}
.toggle-mnu span {
  position: relative;
  display: block;
}
.toggle-mnu span,
.toggle-mnu span:after,
.toggle-mnu span:before {
  width: 100%;
  height: 2px;
  background-color: #fff;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -o-transition: all 0.3s;
  -ms-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-border-radius: 2px;
  border-radius: 2px;
}
.toggle-mnu.on span {
  background-color: transparent;
}
.toggle-mnu.on span:before {
  -webkit-transform: rotate(45deg) translate(-1px, 0px);
  -moz-transform: rotate(45deg) translate(-1px, 0px);
  -o-transform: rotate(45deg) translate(-1px, 0px);
  -ms-transform: rotate(45deg) translate(-1px, 0px);
  transform: rotate(45deg) translate(-1px, 0px);
}
.toggle-mnu.on span:after {
  -webkit-transform: rotate(-45deg) translate(6px, -7px);
  -moz-transform: rotate(-45deg) translate(6px, -7px);
  -o-transform: rotate(-45deg) translate(6px, -7px);
  -ms-transform: rotate(-45deg) translate(6px, -7px);
  transform: rotate(-45deg) translate(6px, -7px);
}
.main-mnu {
  position: absolute;
  left: -186px;
  z-index: 5;
  padding: 15px;
  -webkit-box-shadow: inset 0 -2px 0 0 #1f2a36;
  box-shadow: inset 0 -2px 0 0 #1f2a36;
}
.main-mnu-top {
  top: 40px;
  display: none;
  background-color: rgba(36,51,67,0.8);
}
.main-mnu-bottom {
  top: -320px;
  display: none;
  background-color: #243343;
}
.main-mnu a {
  color: #f0f8ff;
  font-family: "RalewayRegular", sans-serif;
  font-size: 20px;
  line-height: 48px;
}
/*mobile menu end*/
a,
button,
.button {
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.before,
.main-head::before,
.main-head::after {
  content: '';
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
}
.main-head {
  position: relative;
  background-image: url("/img/header_bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  background-size: cover;
}
.main-head::before {
  opacity: 0.85;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=85)";
  filter: alpha(opacity=85);
  background-color: #394b5f;
  z-index: 1;
}
.main-head::after {
  height: auto;
  min-height: 160px;
  background-image: url("/img/top_gradient.png");
  background-repeat: repeat-x;
  z-index: 2;
}
.block-header {
  position: relative;
  padding-top: 48px;
  z-index: 3;
}
.logo {
  width: 100px;
  height: 48px;
}
.menu {
  height: 48px;
  margin-bottom: 106px;
}
.menu ul {
  list-style: none;
}
.menu ul li {
  display: inline-block;
  margin-right: 11px;
}
.menu ul li:last-of-type {
  margin-right: 0;
}
.menu ul li a {
  color: #f9f9f9;
  text-decoration: none;
  font-size: 14px;
  line-height: 48px;
  vertical-align: middle;
}
.menu ul li a:hover {
  color: #f39f18;
  text-decoration: underline;
}
.active {
  color: #f39f18 !important;
  text-decoration: underline !important;
}
.tel {
  display: inline-block;
  width: 100%;
  color: #f9f9f9;
  text-align: right;
  font-family: "RobotoBold", sans-serif;
  font-size: 20px;
  line-height: 48px;
  vertical-align: middle;
}
.gamburger {
  line-height: 48px;
  font-size: 20px;
  color: #f9f9f9;
}
.order-form {
  width: 100%;
  margin-bottom: 123px;
  padding: 25px 31px 41px;
  background-color: rgba(36,51,67,0.8);
  -webkit-box-shadow: inset 0 -2px 0 0 #1f2a36;
  box-shadow: inset 0 -2px 0 0 #1f2a36;
}
.order-form .header {
  margin-bottom: 26px;
  color: #f9f9f9;
  font-family: "PFCentroSlabProMedium", sans-serif;
  font-size: 26px;
}
.order-form p {
  margin: 2px 0;
  color: #97a4b2;
  font-size: 14px;
  line-height: 1.4;
}
.order-form p:nth-of-type(n+2) {
  margin: 26px 0 6px;
}
.order-form input {
  width: 100%;
  -webkit-box-shadow: inset 0 2px 0 0 #1f2a36;
  box-shadow: inset 0 2px 0 0 #1f2a36;
  border: none;
  background-color: #34465a;
}
.order-form input:focus {
  outline-color: #f39f18;
}
.order-form button {
  margin-top: 42px;
}
button,
.button {
  width: 180px;
  margin: auto;
  padding: 7px 0;
  display: block;
  color: #201a11;
  font-family: "PFCentroSlabProBold", sans-serif;
  background-color: #f39f18;
  -webkit-box-shadow: inset 0 -2px 0 0 #b97200;
  box-shadow: inset 0 -2px 0 0 #b97200;
  border: none;
}
button:hover,
.button:hover {
  background-color: #b5760f;
}
.button {
  display: inline-block;
  width: auto;
  padding-left: 24px;
  padding-right: 24px;
  color: #000 !important;
  text-decoration: none !important;
}
.btn-wild-sect {
  width: 100%;
  padding: 65px 0;
}
.btn-narrow {
  font-size: 22px;
}
.logo-text {
  margin-top: 112px;
}
.logo-text h1 {
  margin-bottom: 39px;
  color: #f9f9f9;
  font-size: 47px;
  font-family: "PFCentroSlabProMedium", sans-serif;
  border-bottom: 7px solid #f39f18;
}
.logo-text ul {
  padding-left: 22px;
  list-style: none;
}
.logo-text ul li {
  position: relative;
  margin-bottom: -3px;
}
.logo-text ul li:before {
  content: '';
  position: absolute;
  top: 14px;
  left: -22px;
  width: 0px;
  height: 0px;
  border-top: 5px solid transparent;
  border-left: 15px solid #f39f18;
  border-bottom: 5px solid transparent;
}
.logo-text ul li a {
  color: #f9f9f9;
  font-size: 17px;
  font-family: "RalewaySemiBold", sans-serif;
}
.arrow-down {
  position: relative;
  bottom: 84px;
  width: 40px;
  height: 40px;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  background-image: url("/img/arrow_down.png");
  opacity: 0.5;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
  filter: alpha(opacity=50);
  -webkit-transition: opacity 0.5s ease;
  -moz-transition: opacity 0.5s ease;
  -o-transition: opacity 0.5s ease;
  -ms-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
  cursor: pointer;
}
.arrow-down:hover {
  opacity: 1;
  -ms-filter: none;
  filter: none;
}
/*============================================*/
/*always bottom*/
.hidden {
  display: none !important;
}
